.tune__top {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 18px;
    &--small {
        font-size: 16px;
        margin: 20px 0;
        margin-left: 40px;
    }
}

.tune__top-title {
    color: #9B9B9B;
}

.tune__top-link {
    margin-left: 5px;
}

.tune__block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 40px 0;
}

.tune__label {
    color: $darkblue;
    &--check {
        position: relative;
        margin: 10px 0;
        margin-left: 40px;
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: -40px;
            transform: translateY(-50%);
            display: block;
            width: 30px;
            height: 30px;
            border: 1px solid $darkblue;
            border-radius: 50%;
        }
        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: -34px;
            transform: translateY(-50%);
            display: none;
            width: 18px;
            height: 18px;
            background-color: $darkblue;
            border-radius: 50%;
        }
    }
}

.tune__title {
    color: $darkblue;
    font-weight: 600;
    margin-bottom: 10px;
    &--big {
        font-size: 18px;
    }
}

.tune__subtext {
    color: #9B9B9B;
}

.tune__input {
    width: 100%;
    background: none;
    border: 1px solid $darkblue;
    border-radius: 30px;
    padding: 10px 40px;
    font-size: 14px;
    color: $darkblue;
    letter-spacing: 1px;
    outline: none;
    &--check {
        &:checked {
            &+.tune__label--check {
                &:after {
                    display: block;
                }
            }
        }
    }
    &--small {
        width: 60%;
        padding: 10px 80px;
        margin-right: 43px;
        text-align: center;
    }
    &--file {
        padding: 10px 20px;
        width: initial;
        &:hover {
            background-color: $darkblue;
            color: #fff;
        }
    }
}

.tune__currency {
    display: block;
    background-color: $darkblue;
    color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    width: 43px;
    height: 43px;
    font-size: 22px;
    padding-top: 5px;
    text-align: center;
}

.tune__button {
    display: block;
    background-color: $darkblue;
    color: #fff;
    border-radius: 30px;
    position: absolute;
    top: 0;
    right: 0;
    padding: 10px 20px;
    font-size: 15.5px;
    transition: .3s;
    &--full {
        position: initial;
        width: 60%;
        margin-top: 10px;
        text-align: center;
        &:hover {
            color: #fff;
            background-color: $blue;
        }
    }
    &--add {
        position: initial;
        margin-left: 10px;
        &:hover {
            color: #fff;
            background-color: $blue;
        }
    }
}

.tune__sub {
    color: #9B9B9B;
    font-size: 14px;
    margin-top: 10px;
    margin-left: 20px;
}

.tune__link {
    color: $blue;
    margin-bottom: 10px;
}

.tune__group {
    position: relative;
    width: 50%;
    &--full {
        width: 100%;
        .tune__button {
            padding: 10px 40px;
            transition: .3s;
            &:hover {
                color: #fff;
                background-color: $blue;
            }
        }
    }
}

.tune__row {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    .tune__input {
        width: 60%;
        &--temperature {
            width: 25%;
            margin: 0 10px;
        }
    }
    &--tune {
        justify-content: space-around;
        margin: 10px 0;
    }
}

.reset {
    min-width: 43px;
    height: 43px;
    color: $darkblue;
    text-align: center;
    background-color: #ccc;
    border-radius: 50%;
    margin-left: 15px;
    font-size: 24px;
    padding-top: 3px;
}

.tune__check {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.full {
    width: 100%;
    margin-top: 10px;
}

.map__block {
    width: 100%;
    height: 300px;
    margin-bottom: 20px;
}

.nav-tabs .nav-link {
    border: 1px solid $darkblue;
    margin-right: 10px;
    border-radius: 30px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    transition: .3s;
    &:hover {
        background-color: $darkblue;
        color: #fff;
    }
    &.active {
        background-color: $darkblue;
        color: #fff;
    }
}

.tab-pane {
    color: $darkblue;
    padding: 20px;
}

.tune__list {
    margin-top: 10px;
    padding-left: 20px;
    color: #9B9B9B;
    font-size: 14px;
}

.tune__inputs {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.tune__upload {
    flex: 0 0 15%;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 10px 0;
    &:hover {
        .tune__input {
            &--file {
                transition: .3s;
                background-color: $darkblue;
                color: #fff;
            }
        }
    }
    .tune__label {
        margin: 0 10px;
    }
}

.tune__subtext {
    font-size: 14px;
    color: #9B9B9B;
}

.tune__file {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}