.dashboard__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    opacity: 0;
    z-index: -1;
    transition: .3s;
    &--active {
        opacity: 1;
        z-index: 2;
    }
}

.dashboard {
    position: absolute;
    height: 100%;
    width: 20%;
    background-color: $darkblue;
    border-right: 2px solid $blue;
    @include desktop {
        width: 30%
    }
    @include tablets {
        width: 40%;
    }
    @include phones {
        width: 100%;
        border-right: none;
    }
}

.dashboard__block {
    position: absolute;
    right: 0;
    height: 100%;
    width: 80%;
    @include desktop {
        width: 70%
    }
    @include tablets {
        width: 60%;
    }
    @include phones {
        display: none;
    }
}

.dashboard__menu {
    margin-top: 100px;
}

.dashboard__close {
    position: absolute;
    top: 20px;
    left: 20px;
}

.dashboard__icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    position: relative;
    width: 20px;
    height: 100%;
}

.dashboard__icon {
    width: 20px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    opacity: 1;
    transition: .3s;
    &--hidden {
        opacity: 0;
    }
}

.dashboard__link {
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 20px;
    transition: .3s;
    &:hover {
        color: $darkblue;
        background-color: #EBEBEB;
        & .dashboard__icon {
            opacity: 0;
            &--hidden {
                opacity: 1;
            }
        }
    }
}

.dashboard__item {
    &--active {
        .dashboard__link {
            color: $darkblue;
            background-color: #EBEBEB;
        }
        .dashboard__icon {
            opacity: 0;
            &--hidden {
                opacity: 1;
            }
        }
    }
}

.panel {
    background-color: #fff;
    border-radius: 3px;
    margin-bottom: 30px;
    &--profile {
        min-height: calc(100% - 227px);
    }
}

.panel__title {
    background-color: $darkblue;
    color: #fff;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    font-size: 18px;
    height: 55px;
    padding: 0 25px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include phones {
        font-size: 14px;
    }
}

.amount {
    font-size: 40px;
    line-height: 1;
    margin-left: auto;
    margin-right: 10px;
}

.panel__subtitle {
    font-size: 14px;
    line-height: 1.1;
    @include phones {
        display: none;
    }
}

.panel__top {
    background-color: #F5F5F5;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    margin-top: 40px;
}

.panel__text {
    padding: 17px 30px;
    min-height: 162px;
    color: $darkblue;
}

.panel__filters {
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.panel__filter {
    color: #fff;
    font-size: 14px;
    margin: 0 5px;
    transition: .3s;
    &:hover {
        color: $blue;
    }
}

.footer {
    width: 100%;
    height: 70px;
    background-color: #CCCACD;
}

.channel__spread {
    padding: 55px 20px;
    @include desktop {
        padding: 35px 20px;
    }
}

.spread__bar {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: #CCCACD;
}

.spread__numbers {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.spread__number {
    color: $blue;
    font-size: 36px;
    border: 0;
    width: 100px;
    text-align: center;
    &--right {
        color: #CCCACD;
    }
}

.spread__status {
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
    height: 2px;
    background-color: $blue;
}

.spread__dot {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $blue;
}

.spread__labels {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.spread__label {
    color: $blue;
    &--right {
        color: #CCCACD;
    }
}

.circles {
    position: relative;
    width: 100%;
    height: 100%;
    @include phones {
        width: 70%;
        margin: 0 auto;
    }
}

.circles__title {
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $darkblue;
    font-size: 20px;
    @include laptops {
        font-size: 36px;
    }
    @include tablets {
        font-size: 24px;
    }
}

.benefits__list {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    @include phones {
        padding: 20px 40px;
    }
}

.benefit__dot {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: $blue;
    margin-right: 10px;
}

.benefit__amount {
    font-size: 18px;
    line-height: 1;
}

.benefits__item {
    font-size: 14px;
    margin-bottom: 10px;
    display: flex;
    color: $blue;
    justify-content: center;
    align-items: flex-start;
    &--grey {
        color: #CCCACD;
        & .benefit__dot {
            background-color: #CCCACD;
        }
    }
    &--blue {
        color: $darkblue;
        & .benefit__dot {
            background-color: $darkblue;
        }
    }
    &:last-child {
        margin-bottom: 0;
    }
}

.ui-widget-content {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: #CCCACD;
}

.ui-slider .ui-slider-handle {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $blue;
    outline: none;
}

.ui-widget-header {
    background-color: $blue;
    min-height: 2px;
}