.panel__input {
    position: absolute;
    background: #f5f5f5;
    width: 35%;
    top: 0;
    right: 15px;
    border-top-right-radius: 3px;
    .input__date {
        padding: 14px 30px;
        z-index: 2;
    }
    .input__subtitle {
        top: 50%;
        transform: translateY(-50%);
        z-index: 0;
    }
    &--full {
        position: initial;
        top: initial;
        right: initial;
        width: 100%;
        height: 55px;
    }
}

.panel__link {
    position: absolute;
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
}

.chart__container {
    position: relative;
    width: 100%;
    height: 400px;
}

.search__field {
    position: relative;
}

.search__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
}

.search__input {
    width: 100%;
    background: none;
    border: 1px solid $darkblue;
    border-radius: 30px;
    padding: 5px 40px;
    padding-left: 60px;
    font-size: 14px;
    color: $darkblue;
    letter-spacing: 1px;
    outline: none;
}

.search__input::-webkit-input-placeholder {
    color: $darkblue;
}

.search__input::-moz-placeholder {
    color: $darkblue;
}

.search__input:-moz-placeholder {
    color: $darkblue;
}

.search__input:-ms-input-placeholder {
    color: $darkblue;
}

.settings__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
}

.settings__item {
    margin: 0 20px;
    position: relative;
    width: 15px;
    height: 20px;
}

.settings__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: .3s;
    &--hidden {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: .3s;
    }
}

.settings__link {
    &:hover {
        & .settings__icon {
            opacity: 0;
            &--hidden {
                opacity: 1;
            }
        }
    }
}

.filter__block {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 400px;
    & .settings__icon {
        left: 0;
        &--hidden {
            left: 0;
        }
    }
}

.filter__input {
    width: 100%;
    background: none;
    border: 1px solid $darkblue;
    border-radius: 30px;
    padding: 5px 20px;
    font-size: 16px;
    color: #fff;
    letter-spacing: 1px;
    outline: none;
    transition: .3s;
    margin-left: 20px;
    &:focus {
        background-color: #fff;
        color: $darkblue;
    }
}

.company__table {
    font-size: 14px;
}

.table__icon {
    opacity: 1;
    transition: .3s;
    &--hidden {
        position: absolute;
        top: 5px;
        left: 0;
        opacity: 0;
    }
}

.table__link {
    position: relative;
    &:hover {
        & .table__icon {
            opacity: 0;
            &--hidden {
                opacity: 1;
            }
        }
    }
    &--active {
        & .table__icon {
            opacity: 0;
            &--hidden {
                opacity: 1;
            }
        }
    }
}

.line {
    width: 100%;
    height: 1px;
    background-color: #9B9B9B;
    margin: 10px 0;
}

.company__table table {
    margin-top: 0;
}