.referal__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include laptops {
        flex-direction: column;
    }
}

.referal__avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
}

.referal__pic {
    width: 100%;
}

.referal__table {
    @include tablets {
        display: none;
    }
    &--mobile {
        display: none;
        .referal__avatar {
            width: 85px;
            height: 85px;
            border-radius: 50%;
            overflow: hidden;
            margin-bottom: 20px;
        }
        .referal__user {
            display: flex;
            flex-direction: column;
            background-color: #fff;
            justify-content: center;
            align-items: center;
            margin-top: 30px;
            padding: 20px 0;
            border-radius: 5px;
            padding-bottom: 0;
        }
        .referal__row {
            text-align: center;
            padding: 10px 0;
            width: 100%;
            background-color: #F5F5F5;
            &--light {
                background-color: #fff;
            }
        }
        .referal__login {
            color: $darkblue;
        }
        .referal__email {
            color: $darkblue;
        }
        .referal__date {
            color: $darkblue;
        }
        .referal__benefit {
            color: $darkblue;
        }
        @include tablets {
            display: initial;
        }
    }
}

.referal__title {
    color: $darkblue;
}

.referal__link {
    width: 50%;
    @include laptops {
        width: 100%;
        margin: 15px 0;
    }
}

.referal__input {
    width: 100%;
    background: transparent;
    border: 1px solid $darkblue;
    border-radius: 30px;
    padding: 10px 30px;
    color: $darkblue;
    display: block;
    outline: none;
}

.table {
    margin-top: 50px;
    text-align: center;
    @include laptops {
        font-size: 14px;
    }
}

thead {
    color: $darkblue;
}

th {
    color: $darkblue;
    background-color: #F5F5F5;
    border-left: 1px solid rgba(204, 202, 205, 0.658);
}

th:first-child {
    border-left: none;
}

.table td {
    color: $darkblue;
    background-color: #F5F5F5;
    border-left: 1px solid rgba(204, 202, 205, 0.658);
    vertical-align: middle;
}

td:first-child {
    border-left: none;
}

.table-light td {
    background-color: #fff;
    color: #9B9B9B;
}

.table-lightbg td {
    background-color: #fff;
}

.table thead th {
    font-weight: 300;
    border-bottom: none;
}

.table thead th:first-child {
    border-top-left-radius: 5px;
}

.table thead th:last-child {
    border-top-right-radius: 5px;
}

.table tr:last-child td:first-child {
    border-bottom-left-radius: 5px;
}

.table tr:last-child td:first-child {
    border-bottom-right-radius: 5px;
}

.status {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $darkblue;
    margin: 0 auto;
    &--ignored {
        background-color: #EBEBEB;
    }
}