.info__block {
    height: 92%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.info__panel {
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
    border-radius: 3px;
    color: $darkblue;
    &--date {
        margin-bottom: 20px;
        padding: 0;
        position: relative;
    }
    &--balance {
        background-color: $darkblue;
        color: #fff;
    }
    &--benefit {
        background-color: $blue;
        color: #fff;
    }
    &--loss {
        background-color: #D0021B;
        color: #fff;
    }
}

.input__date {
    width: 100%;
    background-color: transparent;
    border: none;
    padding: 20px 30px;
    color: $darkblue;
    z-index: 2;
}

.input__subtitle {
    position: absolute;
    right: 20px;
    font-size: 12px;
    color: #9B9B9B;
    z-index: 0;
}

.info__right-side {
    font-size: 30px;
}

.table {
    text-align: center;
    margin-bottom: 0;
    @include laptops {
        font-size: 14px;
    }
}

thead {
    color: $darkblue;
}

th {
    color: $darkblue;
    background-color: #F5F5F5;
    border-left: 1px solid rgba(204, 202, 205, 0.658);
}

th:first-child {
    border-left: none;
}

.table td {
    color: $darkblue;
    background-color: #F5F5F5;
    border-left: 1px solid rgba(204, 202, 205, 0.658);
    vertical-align: middle;
}

td:first-child {
    border-left: none;
}

.table-light td {
    background-color: #fff;
}

.table thead th {
    font-weight: 300;
    border-bottom: none;
}

.table thead th:first-child {
    border-top-left-radius: 5px;
}

.table thead th:last-child {
    border-top-right-radius: 5px;
}

.table tr:last-child td:first-child {
    border-bottom-left-radius: 5px;
}

.table tr:last-child td:first-child {
    border-bottom-right-radius: 5px;
}

.pagination__block {
    padding: 20px 30px;
    border-top: 1px solid rgba(204, 202, 205, 0.658);
}

.pagination {
    justify-content: center;
}

.page-link {
    border: none;
    color: #9B9B9B;
    transition: .3s;
    &:hover {
        color: $darkblue;
        background: none;
    }
}

.campaign__table {
    margin-top: 0;
}