.profile__user {
    padding: 20px 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.user__avatar {
    margin-right: 20px;
    max-width: 127px;
    max-height: 127px;
    border-radius: 50%;
    overflow: hidden;
    &--small {
        max-width: 41px;
        max-height: 41px;
    }
}

.user__info {
    color: $darkblue;
}

.user__pic {
    max-width: 100%;
    height: auto;
}

.profile__group {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 20px 40px;
}

.profile__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    color: $darkblue;
    &--phone {
        margin-bottom: 80px;
    }
    @include phones {
        flex-direction: column;
        align-items: flex-start;
    }
}

.profile__label {
    margin-right: 30px;
    margin-bottom: 0;
    line-height: 1.1;
    @include phones {
        margin-bottom: 10px;
    }
}

.profile__input {
    background: transparent;
    border: 1px solid $darkblue;
    border-radius: 30px;
    color: $darkblue;
    padding: 10px 30px;
    width: 60%;
    outline: none;
    @include desktop {
        width: initial;
    }
    @include laptops {
        width: 65%;
    }
    @include phones {
        width: 100%;
    }
}

.profile__note {
    position: absolute;
    top: 50px;
    right: 10px;
    width: 55%;
    color: $blue;
    font-size: 14px;
    @include phones {
        width: 100%;
        font-size: 12px;
        right: 0;
        top: 80px;
    }
}

.phone__pic {
    position: absolute;
    right: 20px;
    @include phones {
        top: 35px;
    }
}