.progress {
    padding: 30px 20px;
}

.progress__titles {
    background-color: #F5F5F5;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 10px;
}

.circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
}

.title__text {
    font-size: 14px;
    margin: 0 10px;
}

.item__desc {
    display: none;
    min-width: 222px;
    position: absolute;
    padding: 10px 20px;
    border: 1px solid rgb(216, 212, 212);
    top: -85px;
    left: -20px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color: #fff;
    border-radius: 3px;
    z-index: 10;
}

.title__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &--1 {
        .circle {
            background-color: $darkblue;
        }
        .title__text {
            color: $darkblue;
        }
        .item__desc {
            color: $darkblue;
        }
    }
    &--2 {
        .circle {
            background-color: $blue;
        }
        .title__text {
            color: $blue;
        }
        .item__desc {
            color: $blue;
        }
    }
    &--3 {
        .circle {
            background-color: #7ED321;
        }
        .title__text {
            color: #7ED321;
        }
        .item__desc {
            color: #7ED321;
        }
    }
    &:hover {
        cursor: pointer;
        .item__desc {
            display: flex;
        }
    }
}

.desc__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
}

.desc__amount {
    font-size: 24px;
}

.desc__text {
    font-size: 12px;
    color: #9B9B9B;
}

.progress__row {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &::after {
        content: "";
        display: table;
        clear: both;
    }
    &:last-child {
        margin-bottom: 0;
    }
}

.progress__bar {
    flex: 0 0 65%;
}

.progress__item {
    height: 4px;
    float: left;
    &--1 {
        background-color: $darkblue;
        width: 60%;
    }
    &--2 {
        background-color: $blue;
        width: 10%;
    }
    &--3 {
        background-color: #7ED321;
        width: 30%;
    }
}

.progress__title {
    position: relative;
    width: 100%;
}

.progress__link {
    font-size: 12px;
    width: 100%;
    margin-left: 10px;
    color: #9B9B9B;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:hover {
        color: #9B9B9B;
    }
}

.options {
    font-size: 12px;
    display: none;
    position: absolute;
    top: -146px;
    right: -265px;
    min-width: 250px;
    z-index: 10;
    background-color: #fff;
    padding: 30px;
    border: 1px solid rgb(216, 212, 212);
    border-radius: 5px;
    opacity: 0;
    transition: .3s;
    &--active {
        opacity: 1;
    }
}

.options__pic {
    position: absolute;
    top: 50%;
    left: -20px;
    transform: translateY(-50%);
    opacity: 0;
    transition: .3s;
}

.options__item {
    position: relative;
    margin-bottom: 10px;
    &:hover {
        & .options__pic {
            opacity: 1;
        }
    }
    &--active {
        & .options__pic {
            opacity: 1;
        }
        .options__link {
            color: $darkblue;
        }
    }
}

.options__link {
    color: #9B9B9B;
    transition: .3s;
    &:hover {
        color: $darkblue;
    }
}