.form__elem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: $darkblue;
}

.form__title {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 30px;
    @include phones {
        margin-bottom: 10px;
    }
}

.form__links {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.form__link {
    padding: 15px 0;
    min-width: 50%;
    text-align: center;
    border-bottom: 1px solid $darkblue;
    &--active {
        border-bottom: 3px solid $darkblue;
    }
}

.form__row {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    &--btns {
        justify-content: space-between;
        margin-top: 10px;
        @include tablets {
            flex-direction: column;
        }
    }
    &--money {
        justify-content: space-around;
    }
    &--pullright {
        justify-content: flex-end;
        margin-top: 10px;
    }
    @include phones {
        flex-direction: column;
        margin: 10px 0;
    }
}

.form__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    @include phones {
        margin-left: 0;
        margin-top: 10px;
    }
}

.social__pic {
    opacity: 1;
    z-index: 2;
    transition: .3s;
    &:hover {
        opacity: 0;
    }
    &--hidden {
        position: absolute;
        top: 1px;
        left: 1px;
        z-index: 0;
    }
}

.social__item {
    margin: 0 10px;
    position: relative;
}

.form__text {
    text-align: center;
    &--grey {
        color: #9B9B9B;
        text-align: left;
        font-size: 14px;
        @include tablets {
            font-size: 12px;
            margin-bottom: 10px;
        }
    }
    &--incorrect {
        color: #D0021B;
        font-size: 14px;
    }
}

.form__input {
    width: 100%;
    text-align: center;
    background: none;
    border: 1px solid $darkblue;
    border-radius: 30px;
    padding: 10px 40px;
    margin: 10px 0;
    color: $darkblue;
    letter-spacing: 1px;
    outline: none;
    &--incorrect {
        border-color: #D0021B;
    }
    &--money {
        width: 60%;
        background-color: #F5F5F5;
        border-color: #9B9B9B;
        @include phones {
            width: 100%;
        }
    }
}

.home {
    opacity: 1;
    transition: opacity .3s;
    &--hidden {
        transition: opacity .3s;
        opacity: 0;
        position: absolute;
        left: 0;
    }
}

.form__reset-block {
    position: relative;
    &:hover {
        & .home {
            opacity: 0;
        }
        & .home--hidden {
            opacity: 1;
        }
    }
}

.btn-reset {
    color: $blue;
    transition: .3s;
    &:hover {
        color: $darkblue;
    }
    &--incorrect {
        color: #D0021B;
    }
    @include tablets {
        margin-bottom: 20px;
    }
}

.form__input::-webkit-input-placeholder {
    color: #CCCACD;
}

.form__input::-moz-placeholder {
    color: #CCCACD;
}

/* Firefox 19+ */

.form__input:-moz-placeholder {
    color: #CCCACD;
}

/* Firefox 18- */

.form__input:-ms-input-placeholder {
    color: #CCCACD;
}