.toggler {
    transition: .3s;
    margin-left: auto;
    &--active {
        transform-origin: 50% 50%;
        transform: rotate(180deg);
    }
}

.panel__body {
    color: $darkblue;
    padding: 20px;
    display: none;
    &--nopadding {
        padding: 0;
        .table {
            margin-top: 0;
        }
    }
}

.money__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
}

.money__amount {
    text-align: center;
    margin-top: 10px;
    background-color: #F5F5F5;
    border-radius: 30px;
    padding: 10px 40px;
    width: 65%;
    @include tablets {
        width: 100%;
    }
}

.money__methods {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.money__method {
    flex: 0 0 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #F5F5F5;
    min-height: 200px;
    margin: 10px;
    border-radius: 10px;
    @include desktop {
        flex: 0 0 30%;
    }
    @include tablets {
        flex: 0 0 45%;
    }
    @include phones {
        flex: 0 0 100%;
    }
}

.method__title {
    text-align: center;
}