.campaign {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;
    padding-bottom: 0;
}

.campaign__item {
    color: #CCCACD;
    border: 1px solid #CCCACD;
    border-radius: 30px;
    padding: 5px 20px;
    margin-bottom: 20px;
    cursor: pointer;
    &--enabled {
        color: $blue;
        border-color: $blue;
    }
    &--disabled {
        color: #D0021B;
        border-color: #D0021B;
    }
}