.points__table {
    @include laptops {
        display: none;
    }
    &--mobile {
        display: none;
        @include laptops {
            display: initial;
        }
    }
}

.points__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include phones {
        flex-direction: column;
    }
}

.points__titie {
    color: $darkblue;
}

.points__form {
    padding: 30px 40px; // margin-top: 40px;
}

.edit__form {
    margin-top: 40px;
}

.edit__wrapper {
    padding: 30px 40px;
    min-height: 205px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.points__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    color: $darkblue;
    &--bottom {
        justify-content: center;
        margin-bottom: 0;
    }
    &--edit {
        margin-bottom: 0;
        justify-content: flex-start;
    }
    @include laptops {
        flex-direction: column;
        align-items: flex-start;
    }
}

.points__input {
    background: transparent;
    border: 1px solid $darkblue;
    border-radius: 30px;
    color: $darkblue;
    padding: 10px 30px;
    width: 70%;
    outline: none;
    @include laptops {
        width: 100%;
    }
}

.points__btns {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &--edit {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    @include desktop {
        width: 100%;
    }
    @include laptops {
        flex-direction: column;
    }
}

.points__logo {
    margin-right: 20px;
}

.points__pic {
    max-width: 100%;
    height: auto;
}

.points__item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 3px;
    margin: 20px 0;
}

.points__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    @include phones {
        flex-direction: column;
    }
}

.points__cell {
    padding: 10px 30px;
    text-align: center;
    color: $darkblue;
    background-color: #fff;
    flex: 0 0 50%;
    &--right {
        background-color: #F5F5F5;
    }
    @include phones {
        width: 100%;
        flex: 0 0 100%;
    }
}

.map__block {
    width: 100%;
    height: 300px;
}