.header {
    color: $darkblue;
    background-color: #EBEBEB;
    min-height: initial;
    position: relative;
    padding-bottom: 0;
    &--profile {
        padding: 0;
        background-color: #fff;
        min-height: initial;
        .header__top {
            padding: 10px 0;
            justify-content: center;
        }
        .logo {
            max-width: 197px;
            @include laptops {
                width: 180px;
            }
            @include phones {
                display: none;
            }
            &--hidden {
                display: none;
                @include phones {
                    display: initial;
                    width: 40px;
                }
            }
        }
        .header__icon {
            margin-left: 20px;
            @include laptops {
                width: 20px;
            }
        }
        .header__selector {
            margin-left: 20px;
        }
        .header__user {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: auto;
        }
        .user__avatar {
            margin-right: 10px;
        }
    }
}

.user__name {
    @include laptops {
        display: none;
    }
}

.header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    max-width: 100%;
    height: auto;
    @include laptops {
        width: 250px;
    }
    @include phones {
        width: 170px;
    }
}

.language-field {
    background-color: #EBEBEB;
    min-width: initial;
    color: #9B9B9B;
    border-color: #9B9B9B;
    border-radius: 20px;
}

.dropdown-toggle::after {
    display: none;
}

.arrow {
    transition: .3s;
}

.arrow--flipped {
    transform: rotate(180deg);
}

.dropdown-item {
    color: #9B9B9B;
    text-transform: uppercase;
    margin-bottom: 10px;
    &:hover {
        background: none;
        color: $darkblue;
    }
}

.main__middle {
    margin-bottom: 100px;
    @include laptops {
        margin-bottom: 50px;
    }
}

.footer__main {
    position: absolute;
    width: 100%;
    height: 15px;
    background-color: $darkblue;
    bottom: 0;
}

.copyright {
    color: #9B9B9B;
    text-align: center;
    margin-bottom: 30px;
    @include tablets {
        font-size: 12px;
        margin-bottom: 10px;
    }
}

.copyright__link {
    color: $blue;
    transition: .3s;
    &:hover {
        color: $darkblue;
    }
}

.bottom__line {
    width: 100%;
    height: 15px;
    background-color: $darkblue;
}

.user__link {
    display: flex;
    justify-content: center;
    align-items: center;
}