.section {
    width: 100%;
    padding: rem(30px) 0;
}

.wrapper {
    min-height: 100%;
    position: relative;
}

.main {
    padding: 100px 0;
    padding-bottom: 50px;
}

.content {
    padding: rem(50px) 0;
    min-height: 800px;
}

a {
    text-decoration: none;
    color: $darkblue;
    &:hover {
        color: $darkblue;
        text-decoration: none;
    }
}

.btn {
    display: block;
    cursor: pointer;
    width: 230px;
    background-color: $blue;
    color: #fff;
    border: none;
    border-radius: 30px;
    padding: 10px 0;
    text-transform: uppercase;
    transition: .3s;
    letter-spacing: 1px;
    &:focus {
        outline: none;
    }
    &:hover {
        background-color: $darkblue;
    }
    &--panel {
        width: 200px;
        text-align: center;
        padding: 5px 0;
        margin-left: auto;
        &:hover {
            color: $blue;
            background-color: #fff;
        }
        @include phones {
            width: 130px;
        }
    }
    &--ref {
        width: 200px;
        text-align: center;
        padding: 10px 0;
        margin-left: 0;
        &:focus {
            outline: none;
        }
        &:hover {
            color: #fff;
            background-color: $darkblue;
        }
    }
    &--profile {
        margin-left: auto;
        width: 60%;
        text-align: center;
        padding: 10px 0;
        @include laptops {
            width: 65%;
        }
        @include phones {
            width: 100%;
        }
        &:hover {
            color: #fff;
            background-color: $darkblue;
        }
        &:focus {
            outline: none;
        }
    }
    &--points {
        width: initial;
        padding: 10px 30px;
    }
    &--form {
        width: 220px;
        font-size: 14px;
        @include laptops {
            margin-bottom: 10px;
        }
    }
    &--edit {
        width: 100%;
        margin: 10px 0;
    }
    &--date {
        display: inline-block;
        width: 100px;
    }
}

.section__title {
    text-align: center;
    width: 100%;
}

.separator {
    position: relative;
    width: 100%;
    line-height: 1;
    margin-bottom: 20px;
    &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        height: 1px;
        width: 42%;
        background-color: $darkblue;
    }
    &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        height: 1px;
        width: 42%;
        background-color: $darkblue;
    }
}

.overflow {
    overflow: hidden;
}